@use "./variable" as v;

// メディアクエリ
@mixin mq($breakpoint: md) {
  @media #{map-get(v.$breakpoints, $breakpoint)} {
    @content;
  }
}

@mixin lineHeightSpaceFill($line-height) {
  &::before,
  &::after {
    content: "";
    display: block;
    width: 0;
    height: 0;
  }
  &::before {
    margin-top: calc((1 - #{$line-height}) * 0.5em);
  }
  &::after {
    margin-bottom: calc((1 - #{$line-height}) * 0.5em);
  }
}
