@charset "UTF-8";
/*
  * Reset CSS
*/
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* list Remove default style */
ul,
ol {
  padding: 0;
  list-style: none;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizespeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* フォームリセット */
input,
button,
textarea,
select {
  font: inherit;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
}

/* stylelint-disable */
textarea {
  resize: vertical;
}

/* stylelint-enable */
input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

/* =====================
 * リセットCSS以外のデフォルトスタイルを調整
===================== */
img {
  height: auto;
  vertical-align: top;
}

a {
  display: inline-block;
  color: inherit;
  cursor: pointer;
  text-decoration: none;
  transition: opacity 0.3s;
}
@media (min-width: 768px) {
  a:hover, a:focus {
    opacity: 0.6;
  }
}

input:focus,
button[type=submit]:focus,
textarea:focus,
select:focus {
  outline: 2px solid var(--c-main);
}

/* =====================
 * Baseスタイル
===================== */
:root,
:root[theme=Default] {
  --f-family-main: "Noto Sans JP", sans-serif;
  --f-family-sub: "Fira Sans", sans-serif;
  --f-font-size: 16px;
  --f-line-height: 1.6;
  --f-weight-regular: 400;
  --f-weight-medium: 500;
  --f-weight-semibold: 600;
  --f-weight-bold: 700;
  --f-weight-black: 900;
  --c-base: #fff;
  --c-base-rgb: 255, 255, 255;
  --c-sub: #1a1a1a;
  --c-sub-rgb: 31, 31, 31;
  --c-main: #023e78;
  --c-main-rgb: 2, 62, 120;
  --c-main-02: #002a52;
  --c-main-03: #edf3f8;
  --c-accent: #fff400;
  --c-accent-02: #e61264;
  --c-accent-03: #d33365;
}

html {
  /* ----------------------------
  * リキッドレイアウト rootのfont-sizeを可変にして、単位をremで指定することでレイアウトを可変にする
    // 計算式参考サイト：https://lpeg.info/html/css_calc_font.html
    // font-size: calc([最小値px] + ([最大値] - [最小値]) * ((100vw - [最小画面幅px]) / ([最大画面幅] - [最小画面幅])));
    // clampと併用して上限下限を設定すること。しないと無限に変化し続ける
  * ---------------------------- */
}
@media (max-width: 375px) {
  html {
    font-size: clamp(12px, 4.2666666667vw, 16px);
  }
}
@media (min-width: 375px) {
  html {
    font-size: 16px;
  }
}
@media (min-width: 768px) {
  html {
    font-size: clamp(12px, 12px + 4 * (100vw - 768px) / 332, 16px);
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

